import React from 'react'
import DoughnutChart from '../../Charts/DoughnutChart/DoughnutChart'
import Chart from '../../Charts/Chart'
import Counter from '../../Statistics/Counter/Counter'
import Percent from '../../Statistics/Percent/Percent'
import { container, section, chart } from './StatisticsSection.css'

const StatisticsSection = ({metrics}) => {
return (
  <div className={container}>
    <h2>Indicateurs concernant la totalit du catalogue</h2>
    <div className={section}>

      <div className={chart}>
        <Counter size="large" value={metrics.records.totalCount} title="Enregistrements"/>
      </div>

      <div className={chart}>
        <Chart
          description={'Rpartition des types d\'enregistrements'}
          chart={<DoughnutChart data={metrics.records.partitions.recordType} />} />
      </div>

      <div className={chart}>
        <Chart
          description={'Rpartition des types de meta donne'}
          chart={<DoughnutChart data={metrics.records.partitions.metadataType} />} />
      </div>
    </div>

    <h2>Indicateurs concernant les jeux de donnes</h2>
      <div className={section}>
        <div className={chart}>
          <Percent value={metrics.datasets.partitions['openness'] ? metrics.datasets.partitions['openness'].yes : 0} total={metrics.datasets.totalCount} size="large" icon="unlock alternate icon" title="Pourcentage de donnes ouvertes" />
        </div>

        <div className={chart}>
          <Percent value={metrics.datasets.partitions['download'] ? metrics.datasets.partitions['download'].yes : 0} total={metrics.datasets.totalCount} size="large" icon="download" title="Pourcentage de jeu de donnes tlchargeable" />
        </div>

        <div className={chart}>
          <Chart
            description={'Rpartition des types de donne'}
            chart={<DoughnutChart data={metrics.datasets.partitions.dataType} />} />
        </div>
      </div>
    </div>
  )
}

export default StatisticsSection



