import React from 'react'
import PublishedDatasets from '../Dataset/PublishedDatasets'

const OrganizationDatasets = ({ published, notPublishedYet, publishedByOthers }) => {
  return (
    <div>
      <PublishedDatasets datasets={notPublishedYet} title={'Donnes en attente de publication'} status={'error'} />
      <PublishedDatasets datasets={published} title={'Donnes publies'} status={'success'} />
      <PublishedDatasets datasets={publishedByOthers} title={'Donnes publies par une autre organisation'} status={'warning'} />
    </div>
  )
}

export default OrganizationDatasets



